import { init } from 'snabbdom/build/package/init'
import { classModule } from 'snabbdom/build/package/modules/class'
import { propsModule } from 'snabbdom/build/package/modules/props'
import { styleModule } from 'snabbdom/build/package/modules/style'
import { eventListenersModule } from 'snabbdom/build/package/modules/eventlisteners'
import { h } from 'snabbdom/build/package/h' // helper function for creating vnodes

const patch = init([
    classModule,
    propsModule,
    styleModule,
    eventListenersModule
]);

const app = document.getElementById('app');

const learnVNode = () => {
    let vdata = {
        //  DOM 
        //  Element 
        props: {
            href: 'http://suyan/logo.png'
        },
        //  DOM  Element  setAttribute  removeAttribute 
        attrs: {
            src: 20
        },
        //  css 
        // nomal  selected 
        //  DOM  Element  classList  class
        class: {
            nomal: false,
            selected: true
        },
        // 
        //  Element  style 
        style: {
            fontSize: '20px',
            margin: '20px'
        },
        //  HTMLElement.dataset 
        //  HTMLElement  dataset 
        dataset: {
            name: 'suyan',
            age: 20
        },
        // 
        //  addEventListener 
        on: {
            click() {
                console.log('click');
            }
        },
        // 
        hook: {
            pre() {
                console.log('pre');
            },
            // vnode
            init(vnode) {
                console.log('pre');
            }
        },
        key: 'header'
    };
    let vnode = h('div.wrap', vdata, ['Vue3']);
    patch(app, vnode);
};

const learnHook = () => {
    // 
    const vnode = h('div#app2', {
        on: {
            click: function () {
                alert('Hello suyan');
            }
        },
        hook: {
            //  vnode
            pre() {
                console.log('pre');
            },
            // vnode
            init(vnode) {
                console.log('pre');
            },
            //  DOM element 
            create(emptyVnode, vnode) {
                console.log('create');
            },
            // DOM  DOM 
            insert(vnode) {
                console.log('insert');
            },
            //  DOM 
            prepatch(oldVnode, vnode) {
                console.log('prepatch');
            },
            // DOM 
            update(oldVnode, vnode) {
                console.log('update');
            },
            //  DOM 
            postpatch(oldVnode, vnode) {
                console.log('postpatch');
            },
            //  DOM 
            destory(vnode) {
                console.log('destory');
            },
            remove(vnode, cb) {
                console.log('remove');
            },
            post() {
                console.log('post');
            }
        }
    }, [
        h('span.text', {
            style: {
                fontWeight: 'bold'
            }
        }, ' ')
    ]);
}


